import React from 'react';
import './App.css';
import sound from './animal_bird_duck_quack_003.mp3'


let audio = new Audio(sound)

const Item = (props) => {
    let onSound = () => {
        props.upCount()
        audio.play()
    }

    let classItem = props.visibility
        ? 'photoVisibility'
        : 'photo'

    return (
        <div className={'item'}>
            <img className={classItem}
                 src={'data:image/jpeg;base64,'}
                 alt={'dog?'}
                 onClick={onSound}
            />
        </div>
    )
}

export default Item
